body,
html {
  margin: 0;
  padding: 0;
  border: 0;
  overflow: hidden;
  height: 100%;
  max-height: 100%;
}


.controller {
  /* position: absolute; */
  /* width: 24px;
  height: 12px; */
  /* z-index: 100; */
  /* bottom: 20px;
  left: 50%; */
  /* margin-left: -12px; */
  /* margin-top: 1.5rem; */
  -webkit-animation: bounce-down 1.6s linear infinite;
  animation: bounce-down 1.6s linear infinite;

}

@-webkit-keyframes bounce-down {
 25% {
      -webkit-transform: translateY(-4px);
 }
 50%, 100% {
      -webkit-transform: translateY(0);
 }
 75% {
      -webkit-transform: translateY(4px);
 }
}

@keyframes bounce-down {
  25% {
       -webkit-transform: translateY(-4px);
               transform: translateY(-4px);
  }
  50%, 100% {
       -webkit-transform: translateY(0);
               transform: translateY(0);
  }
  75% {
       -webkit-transform: translateY(4px);
               transform: translateY(4px);
  }
}

#smartImageProject {
  width: 33.5rem;
  height: 20rem;
  position: relative;
  border-radius: 65px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

#smartImageProject:hover {
  -webkit-filter: none;
          filter: none;
}

.centerTextProject {
  position: absolute;
  top: 50%;
  left: 19%;
  width: 100%;
  z-index: 99;
}

.hoverBlur {
  /* filter: blur(4px); */
  border-radius: 65px;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s; /* Animation */
  cursor: pointer;
  margin-left: 1rem;
  margin-right: 1rem;
}

.hoverBlur:hover {
  /* filter: none; */
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  cursor: pointer;
}

.scrollControl {
  overflow-x: hidden;
  overflow-y: inherit;
}

figcaption {
  position: absolute;
  top: 45%;
  left: 42%;
}

@media screen and (max-width: 1024px) {
  figcaption {
    position: absolute;
    top: 10rem;
    left: 22.5rem;
  }
}

@media screen and (max-width: 768px) {
  figcaption {
    position: inherit;
    bottom: inherit;
    margin: 0.25rem 0px;
    font-size: inherit;
    top: auto;
    left: auto;
  }
}

